:root {
  --color: #00acdc;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
}

body {
  grid-template: 60px 1fr / 1fr;
  margin: 0;
  display: grid;
}

.header {
  grid-row: 60px;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header-logo {
  width: 40px;
  height: 40px;
  display: block;
}

.header-logo__img {
  width: 100%;
  height: 100%;
}

.header-nav {
  display: block;
}

.header-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header-nav__item {
  margin-left: 20px;
}

.header-nav__item:first-child {
  margin-left: 0;
}

.header-nav__link {
  color: #060b35;
  text-transform: uppercase;
  white-space: nowrap;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header-nav__link:hover, .is-active {
  color: var(--color);
}

.is-active:after {
  background-color: var(--color);
  content: "";
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 48px 46px;
  margin: 0;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 408px;
  margin: 0;
  padding: 50px 40px;
  font-weight: 400;
  display: flex;
}

.card__image {
  margin-top: 33px;
}

.card__image-photo {
  object-fit: contain;
  max-width: 160px;
  max-height: 134px;
}

.card__title {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin-top: 40px;
  padding-top: 0;
  font-size: 12px;
  font-weight: 500;
}

.card__product-code {
  width: 98px;
  height: 14px;
  margin-top: 4px;
  position: relative;
  left: -33px;
}

.card__product-code-style {
  white-space: nowrap;
  color: #616070;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__grade {
  flex-direction: row;
  justify-content: space-between;
  width: 166px;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__grade-stars {
  box-sizing: border-box;
  width: 96px;
  height: 16px;
  display: inline-flex;
}

.card__grade-stars-list {
  margin: 0;
  list-style: none;
}

.card__grade-stars-list:nth-child(n+1) {
  background-image: url("star-active.edda4c43.svg");
  width: 16px;
  height: 16px;
}

.card__grade-stars-list:nth-child(n+5) {
  visibility: hidden;
}

.card__grade-reviews {
  color: #060b35;
  min-width: 53px;
  height: 16px;
  font-size: 10px;
  line-height: 16px;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  height: 18px;
  margin-top: 24px;
  display: flex;
}

.card__price-style {
  color: #616070;
  min-width: 31px;
  height: 18px;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
  display: block;
}

.card__price-amount-style {
  color: #060b35;
  width: 50px;
  height: 18px;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  border-color: inherit;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transition-duration: .3s;
  transform: scale(1.2);
}

.card__title:hover {
  color: #34568b;
}
